import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';
import { classlist } from '../../../utils/classlist';

const optionVariantsConfig = {
    variants: {
        active: {
            true: 'bg-active-light text-active',
            false: 'hover:bg-disabled-light bg-white',
        },
    },
};

/**
 * @category Form Controls
 * @description A select option component used within the AtSelect component.
 *
 * @Slot - Use this slot to add custom content inside the select option before the text.
 */

@Component({
    tag: 'at-select-option',
    shadow: false,
})
export class AtSelectOptionComponent {
    /**
     * Value of the select option
     */

    @Prop()
    value: string;

    /**
     * Will apply the active styling to the select option
     */

    @Prop()
    is_active?: boolean = false;

    /**
     * Emitted when the select option is clicked
     */
    @Event()
    atuiClick: EventEmitter<string>;

    private handleClick(): void {
        this.atuiClick.emit(this.value);
    }
    render() {
        const getOptionClassname = classlist(
            'transition[background-color,color,box-shadow] text-body focus:ring-active-foreground/40 flex w-full cursor-pointer items-center truncate p-8 font-normal duration-300 ease-in-out focus:ring-2 focus:outline-0 focus:ring-inset',
            optionVariantsConfig,
        );

        const classname = getOptionClassname({
            active: this.is_active,
        });

        return (
            <li
                role="option"
                value={this.value}
                data-name="select-option"
                aria-selected={this.is_active ? 'true' : 'false'}
                tabIndex={0}
                class={classname}
                onClick={() => this.handleClick()}
            >
                <slot></slot>
                {this.value}
            </li>
        );
    }
}
